/* Custom Fonts */
@font-face {
  font-family: 'rubik';
  src: url('../public/fonts/Rubik-Light.ttf');
  font-weight: 300;
}
@font-face {
  font-family: 'rubik';
  src: url('../public/fonts/Rubik-Regular.ttf');
  font-weight: normal;
}
@font-face {
  font-family: 'rubik';
  src: url('../public/fonts/Rubik-Medium.ttf');
  font-weight: 500;
}
@font-face {
  font-family: 'rubik';
  src: url('../public/fonts/Rubik-Bold.ttf');
  font-weight: bold;
}
/* --- Custom Fonts --- */

.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* scroll-bar */
.scroll-bar {
  max-height: 50vh;
  overflow: scroll;
  background-color: #ccc;
  padding: 5px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.scroll-bar::-webkit-scrollbar{
display: none;
}
